:host {
  font-family: var(--font-family);
  font-size: var(--font-size);
  color: var(--color-fg-default);
}

.viewport-margins {
  max-width: 1080px;
  margin: 0 auto;
  padding: 0 1rem;
}

.skip-to-content-link {
  position: absolute;
  transform: translateY(-300%);
  background: var(--color-bg-default);
  left: 0.5rem;
  padding: 0.5rem 1rem;
  margin-top: 0.5rem;
  position: absolute;
}

.skip-to-content-link:focus {
  transform: translateY(0%);
}

a {
  color: var(--color-default);
}

*:focus {
  box-shadow: 0 0 0 2px var(--color-bg-attention),0 0 0 4px var(--color-penn-blue);
  outline: none;
}

.website-header {
  padding-bottom: 0.75rem;
  border-bottom: 2px solid var(--color-bg-subtle);
}

.universal-navigation {
  background: var(--color-penn-blue);
  margin-bottom: 0.75rem;
  padding: 0 0.5rem;
  
}

.universal-navigation ul {
  display: flex;
  align-items: baseline;
  flex-wrap: wrap;
  scrollbar-color: var(--color-penn-red);
  list-style: none;
  padding: 0;
  margin: 0;
}

.universal-navigation li {
  display: inline-block;
}

.universal-navigation a {
  display: inline-block;
  text-transform: uppercase;
  font-size: 0.75rem;
  letter-spacing: 0.05em;
  font-weight: 500;
  color: var(--color-fg-on-emphasis);
  padding: 0.5rem;
  text-decoration: none;
}

.universal-navigation a:hover {
  text-decoration: underline;
  text-decoration-thickness: 2px;
  text-underline-offset: 3px;
}

.universal-navigation__shield-image {
  vertical-align: sub;
  height: 1rem;
  padding-right: 0.25rem;
}

.website-header__logo-name-container {
  display: flex;
  align-items: center;

  @media (max-width: 920px) {
    margin-right: 6rem;
  }
}

.website-header__logo-name-container a {
  line-height: 0;

  @media (max-width: 920px) {
    display: none;
  }
}

.website-header__logo {
  max-width: 240px;
  margin-right: 1rem;
}

.website-header__name {
  font-size: 1.15rem;
  letter-spacing: 1px;
  font-weight: 500;

  @media (min-width: 921px) {
    padding-left: 0.75rem;
    border-left: solid 1px var(--color-penn-blue);
    font-size: 1.5rem;
  }
}

.website-header__name a span + span {
  font-size: 0.875rem;
  font-weight: 400;
}

.website-header__name a:hover span:first-of-type {
  text-decoration: underline;
}

.website-header__name a {
  color: var(--color-penn-blue);
  text-decoration: none;
  display: flex;
  flex-direction: column;
  line-height: 1.1;
}

.website-header__inner-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;

  @media (max-width: 920px) {
    flex-direction: column;
    align-items: flex-start;
  }
}

@media (max-width: 920px) {
  .website-header__navigation {
    width: 100%;
  }
}

.website-header__navigation-list {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;

  @media (max-width: 920px) {
    margin-top: 0.25rem;
    display: none;
  }
}

.website-header__navigation-list--visible {
  @media (max-width: 920px) {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
  }
}

.website-header__navigation-list a {
  @media (max-width: 920px) {
    padding: 0.75rem 0;
  }
}

.website-header__navigation a {
  display: block;
  text-decoration: none;

  @media (min-width: 921px) {
    padding: 0.5rem;
  }
}

.website-header__navigation a:hover {
  text-decoration: underline;
  text-decoration-thickness: 2px;
  text-underline-offset: 2px;
  text-decoration-color: var(--color-fg-accent);
}

.website-header__navigation-button {
  all: unset;
  padding: 0.5rem 1rem;
  color: var(--color-fg-default);
  border-radius: 0.25rem;
  background: var(--color-bg-subtle);
  border-bottom: 2px solid var(--color-fg-muted);

  position: absolute;
  right: 1rem;
  top: 0;
  display: flex;
  align-items: center;
  padding-left: 0.5rem;

  @media (min-width: 921px) {
    display: none;
  }
}

.website-header__navigation-button[aria-expanded=true] svg {
  rotate: 180deg;
}

.website-header__navigation-button:hover {
  cursor: pointer;
}